  .form__input--mail {
    width: 315px;
  }
}
input.form__input::placeholder,
textarea.letter-text::placeholder {
  color: #000000;
}
.form__list {
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
  text-transform: none;
  padding-top: 9px;
}
@media (min-width: 768px) {
  .form__list--checkbox {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
}
@media (min-width: 840px) {
  .form__list--checkbox {
    justify-content: flex-start;
  }
}
.form__item {
  margin-bottom: 36px;
}
.form__item:last-child {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .form__item {
    padding-left: 0;
    height: 48px;
    margin-bottom: 30px;
  }
}
.form__item--checkbox {
  margin-bottom: 47px;
}
@media (min-width: 768px) {
  .form__item--checkbox {
    margin-bottom: 40px;
  }
  .form__item--checkbox:nth-child(odd) {
    margin-right: 60px;
  }
}
@media (min-width: 1200px) {
  .form__item--checkbox {
    margin: 0;
    width: auto;
    margin-right: 68px;
  }
  .form__item--checkbox:nth-child(odd) {
    margin-right: 68px;
  }
  .form__item--checkbox:last-child {
    margin-right: 0;
  }
}
.form__list input {
  position: absolute;
  z-index: -1;
  opacity: 0;
}
.form__radio-label,
.form__checkbox-label {
  display: inline-flex;
  align-items: center;
  position: relative;
  cursor: pointer;
  padding-left: 78px;
  user-select: none;
  outline: none;
}
@media (min-width: 768px) {
  .form__radio-label,
  .form__checkbox-label {
    padding-left: 75px;
  }
}
.form__radio-label::before,
.form__checkbox-label::before {
  content: "";
  width: 56px;
  height: 56px;
  position: absolute;
  background-color: #f2f2f2;
  left: 0;
}
.form__radio-label:hover::before,
.form__checkbox-label:hover::before {
  background-color: #e5e5e5;
}
.form__radio-label:active::before,
.form__checkbox-label:active::before {
  background-color: #d5d5d5;
}
.form__radio-label::before {
  border-radius: 50%;
}
@media (min-width: 768px) {
  .form__radio-label {
    padding-left: 75px;
  }
  .form__checkbox-label {
    padding-left: 81px;
    min-width: 200px;
  }
}
@media (min-width: 1200px) {
  .form__checkbox-label {
    min-width: auto;
  }
}
.radio-indicator,
.chekbox-indicator {
  display: flex;
  align-items: center;
}
.form__radio-input:checked ~ .radio-indicator::before {
  content: "";
  position: absolute;
  left: 19px;
  width: 20px;
  height: 20px;
  background-color: #333333;
  border-radius: 50%;
}
.form__radio-input:checked ~ .radio-indicator:active::before {
  background-color: rgba(51, 51, 51, 0.3);
}
.form__checkbox-input:checked ~ .chekbox-indicator::before {
  content: "";
  position: absolute;
  left: 16px;
  border-left: 4px solid #333333;
  border-bottom: 4px solid #333333;
  transform: rotate(-45deg);
  width: 18px;
  height: 10px;
  top: 1px;
}
.form__checkbox-input:checked ~ .chekbox-indicator:active::before {
  border-bottom-color: rgba(51, 51, 51, 0.3);
  border-left-color: rgba(51, 51, 51, 0.3);
}
.letter-text {
  resize: none;
  padding: 10px 20px;
  min-height: 160px;
}
@media (min-width: 768px) {
  .letter-text {
    padding-top: 17px;
  }
}
.form__button {
  /* margin-bottom: 75px; */
}
@media (min-width: 768px) {
  .form__button {
    margin: 0;
  }
}
.form__button-wrapper {
  padding: 0 20px;
  text-align: center;
}
@media (min-width: 768px) {
  .form__button-wrapper {
    padding: 0;
  }
}
.form__notification {
  display: none;
}
@media (min-width: 768px) {
  .form__notification {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    font-size: 14px;
    line-height: 26px;
  }
}
.form__input-invalid {
  border: 2px solid red;
  padding-left: 19px;
}
@media (min-width: 768px) {
  .form__input-invalid {
    padding-left: 20px;
  }
}
.modal {
  display: none;
  background-color: #ffffff;
  box-shadow: 0 7px 15px rgba(0, 1, 1, 0.15);
  text-align: center;
  width: 100%;
  position: absolute;
  left: 50%;
  /* margin-right: -50%; */
  transform: translateX(-50%);
}
.modal--filure {
  max-width: 400px;
  bottom: 700px;
}
@media (min-width: 768px) {
  .modal--filure {
    bottom: 341px;
  }
}
@media (min-width: 1200px) {
  .modal--filure {
    bottom: 228px;
  }
}
.modal--success {
  max-width: 640px;
  bottom: 331px;
}
@media (min-width: 768px) {
  .modal--success {
    bottom: 502px;
  }
}
.modal__content-wrapper {
  width: 100%;
}
.modal__content-wrapper--success {
