.main-nav {
  text-align: center;
  position: relative;

  max-width: 1200px;
  margin: 0 auto;
}

.site-list {
  font-size: 14px;
  line-height: 26px;
  font-weight: 700;

  @media (max-width: 320px) {
    font-size: 18px;
    line-height: 40px;
  }

  @media (min-width: 768px) {
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
  }
}

.site-list__item {
  background-color: #81b3d2;
  height: 56px;
  position: relative;

  border-bottom: 1px solid rgba(1, 1, 1, 0.2);
  box-shadow: 0 1px 0 rgba(1, 1, 1, 0.2), inset 0 1px 0 rgba(255, 255, 255, 0.2);

  @media (max-width: 767px) {
    &:last-child {
      ::after {
        content: "";
        position: absolute;
        height: 7px;
        width: 100%;
        bottom: 0;

        background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.2) 100%);
      }
    }
  }

  &:hover {
    background-color: #669ec0;
  }

  &:active {
    background-color: #5496bd;
  }

  @media (min-width: 768px) {
    border-bottom: none;
    box-shadow: none;
    width: 20%;
    background-color: #ffffff;

    &:hover,
    &:active {
      background-color: #ffffff;
    }

    &:nth-child(4n+2) {
      margin-right: 20%;
    }
  }

  @media (min-width: 1200px) {
    width: 238px;

    &:nth-child(4n+2) {
      margin-right: 247px;
    }
  }
}

.site-list a {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%;

  color: #ffffff;
  text-decoration: none;

  &:active {
    color: rgba(255, 255, 255, 0.3);
  }

  @media (min-width: 768px) {
    color: #000000;

    &:hover {
      color: #81b3d2;
    }

    &:active {
      color: rgba(0, 0, 0, 0.3);
    }
  }
}

.site-list__item--active a {
  @media (min-width: 768px) {
    color: #766357;

    &:hover {
      color: #604e43;
    }

    &:active {
      color: rgba(118, 99, 87, 0.3);
    }
  }
}

.main-nav__toggle {
  position: absolute;
  bottom: -40px;
  right: 20px;
  /* z-index: 2;
    display: block; */
  width: 23px;
  height: 22px;
  background-color: transparent;
  cursor: pointer;
  border: none;
  outline: none;

  @media (min-width: 768px) {
    display: none;
  }
}


@media (max-width: 767px) {
  .main-nav--closed .site-list {
    display: none;
  }


  .main-nav--nojs .main-nav__toggle {
    display: none;
  }
}

.main-nav--closed .main-nav__toggle {
  position: absolute;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 23px;
    height: 4px;
    background-color: #81b3d2;
    box-shadow: 0 9px 0 0 #81b3d2, 0 18px 0 0 #81b3d2;
  }

  &:hover::before {
    background-color: rgba(129, 179, 210, 0.6);
    box-shadow: 0 9px 0 0 rgba(129, 179, 210, 0.6), 0 18px 0 0 rgba(129, 179, 210, 0.6);
  }

  &:active::before {
    background-color: rgba(129, 179, 210, 0.3);
    box-shadow: 0 9px 0 0 rgba(129, 179, 210, 0.6), 0 18px 0 0 rgba(129, 179, 210, 0.6);
  }
}

.main-nav--opened .main-nav__toggle {
  top: 16px;
  width: 25px;
  height: 25px;

  &::before,
  &::after {
    content: "";
    position: absolute;
    top: 10px;
    right: 0;
    width: 28px;
    height: 4px;
    background-color: #ffffff;
  }

  &::before {
    transform: rotate(45deg);
    box-shadow: none;
  }

  &::after {
    transform: rotate(-45deg);
  }

  &:hover::before,
  &:hover::after {
    background-color: fade(#ffffff, 60%);
  }

  &:active::before,
  &:active::after {
    background-color: fade(#ffffff, 30%);
  }
}
